import { ApolloClient } from 'apollo-client'
import { createHttpLink } from 'apollo-link-http'
import { InMemoryCache } from 'apollo-cache-inmemory'
import VueApollo from 'vue-apollo';

const httpLinkUvstake = createHttpLink({
  uri: 'https://api.studio.thegraph.com/query/12140/uvstake/4.0.0',
  // headers: {
  //   // searchToken: '21222'
  //   'accept-Language': "en-GB"
  // }
})

const httpLinkLpStake = createHttpLink({
  uri: 'https://api.thegraph.com/subgraphs/name/uvswap/uvswap',
  // headers: {
  //   // searchToken: '21222'
  //   'accept-Language': "en-GB"
  // }
})
const httpLinkUvPair = createHttpLink({
  uri: 'https://api.thegraph.com/subgraphs/name/uvswap/uvlp',
  // headers: {
  //   // searchToken: '21222'
  //   'accept-Language': "en-GB"
  // }
})
const cache = new InMemoryCache()

const apolloClientUvstake = new ApolloClient({
  link: httpLinkUvstake,
  cache,
})
const apolloClientLpStake = new ApolloClient({
  link: httpLinkLpStake,
  cache,
})
const apolloClienUvPair = new ApolloClient({
  link: httpLinkUvPair,
  cache,
})
const apolloProvider = new VueApollo({
  defaultClient: apolloClientUvstake,
  clients: {
    uvStakeClient: apolloClientUvstake,
    lpStakeClient: apolloClientLpStake,
    uvPairClient: apolloClienUvPair
  }
})
export default apolloProvider;